import { Button } from "@/components/ui/button";
import { Card, CardContent, CardFooter } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import sanderoImage from '../assets/images/2.jpg';
import clioImage from '../assets/images/6.jpg';
import stepwayImage from '../assets/images/stepway.jpg';
import dusterImage from '../assets/images/1.jpg';
import loganImage from '../assets/images/logan.jpg';
import skodaImage from '../assets/images/skoda.jpg';

const FeaturedCars = () => {
  const cars = [
    {
      id: 1,
      name: "Dacia Sandero",
      image: sanderoImage,
      category: "Economy",
      features: ["5 Seats", "Petrol", "Manual"]
    },
    {
      id: 2,
      name: "Renault Clio",
      image: clioImage,
      category: "Economy",
      features: ["5 Seats", "Petrol", "Air Conditioning", "Manual"]
    },
    {
      id: 3,
      name: "Dacia Stepway Automatique",
      image: stepwayImage,
      category: "SUV",
      features: ["5 Seats", "Gasoil", "Air Conditioning", "Automatique"]
    },
    {
      id: 4,
      name: "Dacia Duster Automatique",
      image: dusterImage,
      category: "SUV",
      features: ["5 Seats", "Gasoil", "Air Conditioning", "2/4"]
    },
    {
      id: 5,
      name: "Dacia Logan",
      image: loganImage,
      category: "Economy",
      features: ["5 Seats", "Petrol", "Air Conditioning", "Manual"]
    },
    {
      id: 6,
      name: "Skoda Fabia",
      image: skodaImage,
      category: "Economy",
      features: ["5 Seats", "Petrol", "Air Conditioning", "Manual"]
    }
  ];

  return (
    <section className="py-16 bg-gray-50">
      <div className="container mx-auto px-4">
        <h2 className="text-3xl font-bold text-center mb-8">Our Featured Vehicles</h2>
        <p className="text-center text-gray-600 mb-12 max-w-2xl mx-auto">
          Choose from our selection of premium vehicles perfect for exploring the beautiful landscapes of Dakhla.
        </p>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {cars.map((car) => (
            <Card key={car.id} className="overflow-hidden">
              <div className="aspect-w-16 aspect-h-9">
                <img
                  src={car.image}
                  alt={car.name}
                  className="object-cover w-full h-full"
                />
              </div>
              <CardContent className="p-6">
                <div className="flex justify-between items-start mb-4">
                  <h3 className="text-xl font-semibold">{car.name}</h3>
                  <Badge variant="secondary">{car.category}</Badge>
                </div>
                <div className="flex flex-wrap gap-2">
                  {car.features.map((feature, index) => (
                    <Badge key={index} variant="outline">
                      {feature}
                    </Badge>
                  ))}
                </div>
              </CardContent>
              <CardFooter className="p-6 pt-0">
                <Button className="w-full">Reserve Now</Button>
              </CardFooter>
            </Card>
          ))}
        </div>
      </div>
    </section>
  );
};

export default FeaturedCars;
